        // img: imgArt1,
        p1:'W dniach 17-23 marca w Warszawie miała miejsce 144. edycja międzynarodowej konferencji - European Study Group for Industry (www.esgi144.pl), w której czynny udział wzięli Krzysiek i Ola.',
        p2:'Inicjatywa ta, zapoczątkowana przez Oxford University ponad 50 lat temu, służy zacieśnieniu współpracy między ośrodkami akademickimi a przedstawicielami przemysłu. Po raz drugi w historii ESGI odbyło się w Polsce, zaś jej organizacji podjęła się ponownie Polska Akademia Nauk. W gmachu IM PANu spotkali się studenci i absolwenci University of Oxford, University of Kiyv, University of Toronto, University of Georgia,  Politechniki Warszawskiej, Uniwersytetu Jagiellońskiego, Akademii Górniczo-Hutniczej oraz Uniwersytetu Wrocławskiego.', 
        p3:'Praca polegała na zgłębianiu rzeczywistych, złożonych problemów zaprezentowanych przez sześć firm. Poruszane tematy obejmowały takie dziedziny jak górnictwo (KGHM), turystyka i hotelarstwo (RoomSage) oraz rynki finansowe. ',
        p4:'Zadaniem ograniczonych jedynie zasobami wiedzy i kreatywnością uczestników było znalezienie optymalnego rozwiązania w postaci modelu spełniającego założenia firm. Istotną składową warsztatów była dyskusja - nad zagadnieniami pracowali nie tylko matematycy specjalizujący się w analizie funkcjonalnej czy topologii, lecz także znawcy (i praktycy) zastosowań matematyki, więc nieuniknione były różnice w spojrzeniu na matematyczne tło problemów. Codzienne raporty z postępów w pracy konfrontowane były z wizją przedsiębiorców, co prowokowało dalsze pytania, zaś pojawiające się wątpliwości umożliwiały dopracowanie modelu.',
