        p1:'W dniach 22-24 marca wybrałam się z Szymonem do Warszawy, na drugą edycję Studenckiej Konferencji Zastosowań Matematyki “Dwumian”, organizowaną przez dwa wydziały Matematyki: Politechniki Warszawskiej i Uniwersytetu Warszawskiego (MiNI PW i MIM UW).', 
        p2:'Konferencja została podzielona na 2 ścieżki:',
        p3:'Specjalność, którą wybraliśmy na studiach licencjackich to “Analiza danych”. Nietrudno więc się domyślić, którą ścieżką poszliśmy 😉 ',
        p4:'Konferencja okazała się być naprawdę ciekawa i bardzo dobrze zorganizowana. Wykłady były prowadzone zarówno przez wybitnych matematyków, jak i przedstawicieli firm (m.in. Oracle czy CD Projekt RED). Nie zabrakło również referatów i plakatów studenckich. Dodatkowo, firma PwC zorganizowała 2,5 godzinne warsztaty “Deep learning w teorii i praktyce – podstawy", na które nie zdążyliśmy się już niestety zapisać. Mały minus dla organizatorów - za brak możliwości uczestnictwa w warsztatach z własnym sprzętem.',
        p5:'W ramach integracji, pierwszego wieczoru zorganizowano wyjście do jednego z klubów na Mokotowie. Drugiego, można było wybrać się na spacer z przewodnikiem lub do Centrum Nauki Kopernik. Wieczorami nie dało się więc nudzić 😀 ',
        p6:'Wracając do (najważniejszej) naukowej strony konferencji, przedstawię pokrótce tematykę trzech wykładów, które spodobały nam się najbardziej:',
        p7:'Pan profesor jest uznawany za autorytet w dziedzinie analizy statystycznej i uczenia maszynowego. W trakcie swojego wykładu, poruszył tematykę danych, charakteryzujących się niewielką liczbą obserwacji (np. 100) i dużą liczbą cech (np. 400 tysięcy). Krótko przedstawił różne podejścia do selekcji istotnych atrybutów oraz budowy modelu na podstawie danych. Zastosował algorytm MCFS-ID, który za pomocą wielu drzew decyzyjnych, weryfikował istotność cech, a także otrzymywał grafy współzależności, które odzwierciedlały ranking interakcji między atrybutami.',
        p8:'Główną specjalizacją dr Miłosia jest “reinforcement learning”, czyli uczenie ze wzmocnieniem. Jest to w pewnym sensie metoda prób i błędów. Za pomocą zestawu kar i nagród, staramy się nauczyć system, co ma robić. Dla przykładu, uczący się robot dostanie nagrodę o wartości 1 (jeśli dotrze do zadanej pozycji) lub –1, czyli karę (jeśli np. uderzy w przeszkodę). W pozostałych przypadkach nagroda ma wartość 0. Dr Miłoś opowiedział nam o konkursie organizowanym przez Uniwersytet Stanforda, w którym miał przyjemność wziąć udział. Polegał on na tym, aby nauczyć szkielet chodzić (od samych podstaw). Okazało się to być nie lada wyzwaniem, w którym główną rolę odgrywał właśnie reinforcement learning.',
        p9:'Dr Burdukiewicz może być niektórym z Was znany ze spotkań STWURa (Stowarzyszenia Wrocławskich Użytkowników R). Jest on bioinformatykiem, z szerokim uśmiechem, zawodowo zajmującym się analizą funkcjonalną białek i medycyną spersonalizowaną.  Na początku wykładu, zaznajomiliśmy się z podstawowymi informacjami na temat aminokwasów, białek i ich rzędowości, a także amyloidów - nieprawidłowych białek, związanych z chorobami takimi jak gruźlica, reumatoidalne zapalenie stawów czy choroba Parkinsona. W dużym skrócie: do przewidywania czy białko jest amyloidem, czy też nie, prowadzący wytrenował predykatory, stosując drzewa losowe i metodę n-gram. Najlepszy okazał się być AmyloGram (zainteresowanych odsyłam do pakietów R-owych: biogram i Amylogram).',
        p10:'Na koniec dodam, że zachęcam wszystkich do brania udziału w konferencjach. Oprócz dodatkowej wiedzy, którą wyniesiecie po takim weekendzie, poznacie też mnóstwo ludzi o podobnych zainteresowaniach, porozmawiacie ze studentami z innych uczelni, być może ktoś was namówi do udziału w ciekawym projekcie lub kolejnej konferencji. No i przede wszystkim - po udanym wyjeździe, spodziewajcie się solidnego przypływu motywacji!',
