import imgArt1 from '../img/articlesImg/IG_LS2.jpeg'
import ArtHeader from '../ArtH';
import ArtImg from '../ArtI';
import ArtParagraph from '../ArtP';
import ArtLi from '../ArtList';
import React, { Component } from 'react';
class Art3 extends Component {
    state = { 
        title: 'LICZĄ SŁOWA II',
        title2: 'Lingwistyczna kartografia  - ciąg dalszy',
        author: 'Iwo Gruca',
        date: '04 - 2019',
        img: imgArt1,
        p1:'W poprzednim małym artykule, do którego serdecznie zapraszam, doszliśmy do wniosku, że mierzenie odległości między słowami ma jak najbardziej sens. Tym razem poznamy jedną z najprostszych oraz najpopularniejszych służących do tego metryk, a mianowicie odległość Levenshteina.',
        p2:'Na samym początku słowo o twórcy. Vladimir Levenshtein był rosyjskim naukowcem urodzonym 20 marca 1935 roku w Moskwie. Umarł 6 września 2017 roku. Specjalizował się głównie   w teorii informacji. Jego pomysł na obliczanie odległości między słowami jest niezwykle prosty i każdy może za jego pomocą wyznaczyć średnicę ulubionej książki!',
        p3:'Zanim przejdziemy do technicznego formalizmu, który uwielbiamy, musimy sobie przypomnieć czym są dla nas słowa. W poprzednim artykule założyliśmy, że będą to skończone ciągi znaków pochodzących z alfabetu. Mając taki model, możemy przystąpić do dalszych działań! Powiemy, że wstawienie nowego znaku do napisu, usunięcie znaku lub zamiana dwóch znaków jest działaniem prostym i teraz uwaga! Odległość Levenshteina pomiędzy dwoma słowami to nic innego, jak najmniejsza liczba operacji prostych potrzebnych do przekształcenia jednego słowa w drugie.',
        p4:'Zobaczmy teraz obliczanie tej metryki w praktyce na dwóch przykładach, Lev(x, y) będzie naszym oznaczeniem odległości Levenshteina pomiędzy słowami x oraz y.',
        p4a:'Przykład 1:',
        p4b:'Lev(kebab, pizza) = 5,',
        p5:'ponieważ każdą literę wyrazu kebab zamieniamy na odpowiednią literę wyrazu pizza (zauważmy przy tym, że oba słowa mają taką samą długość oraz różne litery na poszczególnych miejscach, zatem  ich odległość jest po prostu równoważna liczbie zawartych w nich liter). ',
        p5a:'Przykład 2:',
        p5b:'Lev(dzwonnik, mikrobiologia) = 12,',
        p6:'zamieniamy ostatnie litery k na a (1 operacja), przedostatnie i zostawiamy w spokoju, zamieniamy każdą literę z ciągów dzwonn oraz biolog (6 operacji) oraz usuwamy bądź wstawiamy człon mikro (5 operacji).',
        p7:'Jak widać z obu przykładów obliczanie odległości Levenshteina  jest stosunkowo proste, choć może sprawiać trudności przy dłuższych słowach. To by było na tyle w dzisiejszym odcinku naszej językowej serii.  W kolejnym krótkim artykule spróbujemy odpowiedzieć na pytanie, jak możemy sobie poradzić  z leksykograficzną analizą homografów, tj. wyrazów o takiej samej pisowni, ale różnej wymowie, np.: rozmarzać (wywoływać marzenia) i rozmarzać (roztapiać). Do następnego!',
        items:[
            {id:1, txt: 'Krótka biografia Vladimira Levenshteina: ',href: 'https://en.wikipedia.org/wiki/Vladimir_Levenshtein'},
            {id:2, txt: 'Opis odległości Levenshteina:',href: 'https://pl.wikipedia.org/wiki/Odległość_Levenshteina'},
        ]
    
    }
    // .articlePage__txt
    render() { 
        const {title,title2,img, author,date, p1,p2,p3,p4,p4a,p4b,p5,p5a,p5b,p6,p7,items} = this.state
        return (
            <div className="articlePage">
                <ArtHeader title = {title} title2 = {title2} author = {author} img = {img} date = {date}/>
                <ArtImg img = {img}/>
                <ArtParagraph p = {p1}/>
                <ArtParagraph p = {p2}/>
                <ArtParagraph p = {p3}/>
                <ArtParagraph p = {p4}/>
                <ArtParagraph p = {p4a}/>
                <center><p className = 'articlePage__txtCenter articlePage__txtItalic'>Lev(kebab, pizza) = 5,</p></center>
                <ArtParagraph p = {p5}/>
                <ArtParagraph p = {p5a}/>
                <center><p className = 'articlePage__txtCenter articlePage__txtItalic'>Lev(dzwonnik, mikrobiologia) = 12,</p></center>
                <ArtParagraph p = {p6}/>
                <ArtParagraph p = {p7}/>
                <ul className="artPage__list">
                {items.map(item => <ArtLi key = {item.id} li = {item.txt} href = {item.href}/>)}
                </ul>
            </div>
          );
    }
}
 
export default Art3;